.btn {
	display: inline-block;
	margin-top: 1rem;
	padding: 1rem 3rem;
	background: $dark-gray;
	color: $white;
	cursor: pointer;
	font-size: 1.7rem;
	text-transform: capitalize;
	transition: 0.3s linear;
	border-radius: 0.5rem;

	&:hover {
		background: $orange;
		color: $white;
	}
}
