@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap");
font-face {
  font-family: "BrunoAce";
  src: url("../../fonts/BrunoAceRegular.ttf") format("truetype");
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}
body .bg {
  animation: slide 12s ease-in-out infinite alternate;
  background-image: linear-gradient(60deg, #f2662c 50%, #141617 50%);
  bottom: 0;
  left: -50%;
  opacity: 0.5;
  position: fixed;
  right: -50%;
  top: 0;
  z-index: -1;
}
body .bg2 {
  animation-direction: alternate-reverse;
  animation-duration: 16s;
}
body .bg3 {
  animation-duration: 20s;
}

html {
  background-color: #0a090b;
  background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23512f0f' fill-opacity='0.19' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 6V5zM6 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");
  font-size: 65%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-padding-top: 7rem;
  scrollbar-width: thin;
  scrollbar-color: #f2662c #000;
}
html::-webkit-scrollbar-thumb {
  background-color: #f2662c;
}
html::-webkit-scrollbar-thumb:hover {
  background: #424242;
}
html::-webkit-scrollbar {
  width: 5px;
}
html::-webkit-scrollbar-track {
  background: #000;
}

section {
  padding: 5rem 10%;
}

@keyframes slide {
  0% {
    transform: translateX(-25%);
  }
  100% {
    transform: translateX(25%);
  }
}
@keyframes swing-in-right-fwd {
  0% {
    transform: rotateY(-100deg);
    transform-origin: right;
    opacity: 0;
  }
  100% {
    transform: rotateY(0);
    transform-origin: right;
    opacity: 1;
  }
}
@keyframes slide-out-right {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(1000px);
    opacity: 0;
  }
}
@keyframes swing-in-top-fwd {
  0% {
    transform: rotateX(-100deg);
    transform-origin: top;
    opacity: 0;
  }
  100% {
    transform: rotateX(0deg);
    transform-origin: top;
    opacity: 1;
  }
}
@keyframes slide-out-bck-top {
  0% {
    transform: translateZ(1) translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateZ(-1100px) translateY(-1000px);
    opacity: 0;
  }
}
.heading-title {
  font-family: "BrunoAce";
  text-align: center;
  color: #fff !important;
  margin-bottom: 3rem;
  font-size: 4rem;
  color: #000;
  text-transform: capitalize;
}
.heading-title span {
  font-family: "BrunoAce";
  color: #f2662c;
}

.btn {
  display: inline-block;
  margin-top: 1rem;
  padding: 1rem 3rem;
  background: #141617;
  color: #fff;
  cursor: pointer;
  font-size: 1.7rem;
  text-transform: capitalize;
  transition: 0.3s linear;
  border-radius: 0.5rem;
}
.btn:hover {
  background: #f2662c;
  color: #fff;
}

.contact-info {
  position: fixed;
  top: 0;
  right: 0;
  width: 34rem;
  background: #141617;
  height: 100%;
  text-align: center;
  z-index: 1100;
  padding: 0 2rem;
  padding-top: 5rem;
  display: none;
  -webkit-animation: swing-in-right-fwd 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
  animation: swing-in-right-fwd 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
}
.contact-info.active {
  box-shadow: 0 0 0 100vw rgba(0, 0, 0, 0.7);
  display: block;
}
.contact-info.in-active {
  display: block;
  animation: slide-out-right 0.2s linear both;
}
.contact-info #close-contact-info {
  position: absolute;
  top: 4rem;
  right: 6rem;
  cursor: pointer;
  font-size: 3rem;
  color: #f2662c;
  margin-bottom: 2rem;
  transition: all 0.2s linear;
}
.contact-info #close-contact-info:hover {
  transform: rotate(90deg);
}
.contact-info .content-info {
  padding-top: 10rem;
}
.contact-info .content-info .info {
  padding: 0.8rem 0;
}
.contact-info .content-info .info i {
  height: 5rem;
  width: 5rem;
  line-height: 5rem;
  font-size: 2rem;
  color: #f2662c;
  cursor: pointer;
  text-align: center;
  margin-bottom: 0.2rem;
}
.contact-info .content-info .info i:hover {
  color: #fff;
}
.contact-info .content-info .info p {
  font-size: 2rem;
  color: #f2662c;
  line-height: 2;
  font-family: "Work Sans", sans-serif;
  letter-spacing: 2px;
  font-weight: 500;
  cursor: pointer;
  color: #fff;
  position: relative;
  transition: border-color 0.4s ease-in-out;
}
.contact-info .content-info .info p::after {
  content: "";
  position: absolute;
  border-top: 2px solid rgba(242, 102, 44, 0.2);
  left: 35%;
  bottom: 0;
  width: 30%;
  height: 0px;
  transition: all 0.2s ease-in-out;
}
.contact-info .content-info .info p:hover::after {
  border-top: 2px solid rgba(242, 102, 44, 0.8);
  width: 32%;
  left: 34%;
}
.contact-info .content-info .share {
  padding-top: 5rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
}
.contact-info .content-info .share a {
  height: 5rem;
  width: 5rem;
  line-height: 5rem;
  font-size: 2rem;
  color: #f2662c;
  cursor: pointer;
  text-align: center;
  border-radius: 14px;
  background: linear-gradient(145deg, #151819, #121415);
  box-shadow: 6px 6px 12px #090a0b, -6px -6px 12px #1f2223;
  font-size: 3rem;
  color: #fff;
  transition: color 0.2s linear, box-shadow 0.9s linear;
}
.contact-info .content-info .share a:hover {
  color: #fff;
}
.contact-info .content-info .share a:hover {
  color: #f2662c;
  border-radius: 14px;
  background: #141617;
  box-shadow: inset 6px 6px 12px #090a0b, inset -6px -6px 12px #1f2223;
}

.header {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0px 1px 10px 0px hsla(0deg, 0%, 0%, 0.12), 0px 2px 4px -1px hsla(0deg, 0%, 0%, 0.2);
  padding: 2rem 2%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1000;
  background: transparent;
  transition: all 0.4s cubic-bezier(0.1, 0.6, 0.2, 0.5);
}
.header.active {
  background: #141617;
  padding: 1.5rem 1.5%;
  box-shadow: hsla(0deg, 0%, 0%, 0.14), 0px 5px 22px 4px hsla(0deg, 0%, 0%, 0.12), 0px 7px 8px -4px hsla(0deg, 0%, 0%, 0.2);
}
.header__logo img {
  width: 120%;
  height: auto;
}
.header__logo img.active {
  width: 50%;
  height: auto;
}
.header__navbar a {
  font-size: 2rem;
  color: #000;
  text-transform: capitalize;
  margin: 0 1rem;
  color: #fff;
  font-family: "BrunoAce";
}
.header__navbar a:hover {
  color: #f2662c;
}
@media (max-width: 900px) {
  .header__navbar {
    position: absolute;
    top: 99%;
    left: 0;
    right: 0;
    background: rgba(5, 24, 121, 0.422);
    border-top: 0.1rem solid rgba(0, 0, 0, 0.3);
    border-bottom: 0.1rem solid rgba(0, 0, 0, 0.3);
    transition: 0.4s linear;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 45h;
    background-color: #141617;
    border-radius: 0 0 3rem 3rem;
    display: none;
  }
  .header__navbar.active {
    display: flex;
    animation: swing-in-top-fwd 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
  }
  .header__navbar.in-active {
    display: flex;
    animation: slide-out-bck-top 0.5s cubic-bezier(0.6, -0.28, 0.735, 0.045) both;
  }
}
.header__icons div {
  height: 5rem;
  width: 5rem;
  line-height: 5rem;
  font-size: 2rem;
  color: #f2662c;
  cursor: pointer;
  text-align: center;
  margin-left: 0.3rem;
}
.header__icons div:hover {
  color: #fff;
}
.header__icons #menu-btn {
  display: none;
}
@media (max-width: 900px) {
  .header__icons #menu-btn {
    display: inline-block;
  }
}
@media (min-width: 901px) {
  .header {
    padding: 1.5rem, 2rem;
  }
}
.contact .heading {
  color: #fff;
}
.contact .row {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}
.contact .row .map {
  flex: 1 1 41rem;
  width: 100%;
}
.contact .row form {
  flex: 1 1 41rem;
  padding: 2rem;
}
.contact .row form h3 {
  font-size: 2.5rem;
  color: #000;
  text-transform: capitalize;
}
.contact .row form .box {
  margin: 7rem 0;
  width: 100%;
  padding: 1.5rem 0rem;
  font-size: 1.6rem;
  color: #343a40;
  background: #000;
  border-bottom: 0.1rem solid rgba(0, 0, 0, 0.3);
  border-color: #f2662c;
}
.contact .row form .box:focus {
  border-bottom: 2px solid #f2662c;
}
.contact .row form textarea {
  height: 15rem;
  resize: none;
}

.footer {
  text-align: center;
  padding-bottom: 10rem;
}
.footer .links .btn {
  margin: 0.5rem;
}
.footer .credit {
  font-size: 2rem;
  color: #000;
  text-transform: capitalize;
  margin-top: 2rem;
  padding-top: 1rem;
}
.footer .credit span {
  color: #f2662c;
}

.home {
  padding: 0;
}
.home .slide {
  min-height: 60rem;
  display: flex;
  align-items: center;
  position: relative;
  background-size: cover !important;
  background-position: center !important;
}
.home .slide::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: linear-gradient(180deg, #141617, transparent);
}
.home .slide .content {
  width: 50rem;
  position: relative;
}
.home .slide .content h3 {
  font-family: "BrunoAce";
  color: white;
  font-size: 6rem;
  color: #000;
  text-transform: capitalize;
  text-transform: uppercase;
}
.home .slide .content p {
  font-size: 1.7rem;
  color: #f2662c;
  line-height: 2;
  padding: 1rem 0;
}
.home .swiper-button-next,
.home .swiper-button-prev {
  top: initial;
  bottom: 0;
  left: initial;
  right: 0;
  width: 7rem;
  height: 7rem;
  line-height: 7rem;
  font-size: 3rem;
  background: #141617;
}
.home .swiper-button-next:hover,
.home .swiper-button-prev:hover {
  background: #f2662c;
}
.home .swiper-button-next::after,
.home .swiper-button-prev::after {
  font-size: 2rem;
  color: #fff;
}
.home .swiper-button-prev {
  right: 7rem;
}

.pricing {
  color: #fff;
}
.pricing .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 2rem;
}
.pricing .box-container .box {
  background-color: #141617;
  color: #fff;
  text-align: center;
  padding: 2rem;
  border: 0.1rem solid rgba(0, 0, 0, 0.3);
  box-shadow: hsla(0deg, 0%, 0%, 0.14), 0px 5px 22px 4px hsla(0deg, 0%, 0%, 0.12), 0px 7px 8px -4px hsla(0deg, 0%, 0%, 0.2);
  border-radius: 0.9rem;
}
.pricing .box-container .box i {
  font-size: 2rem;
  color: #fff;
  height: 7rem;
  line-height: 7rem;
  margin-bottom: 1rem;
  background: #f2662c;
  border-radius: 50%;
  width: 7rem;
  box-shadow: hsla(0deg, 0%, 0%, 0.14), 0px 5px 22px 4px hsla(0deg, 0%, 0%, 0.12), 0px 7px 8px -4px hsla(0deg, 0%, 0%, 0.2);
}
.pricing .box-container .box h3 {
  font-size: 2rem;
  color: #000;
  text-transform: capitalize;
  font-weight: 400;
  padding: 2rem 0;
  color: #fff;
}
.pricing .box-container .box .price {
  padding: 1rem 0;
  font-size: 5rem;
  color: #000;
  text-transform: capitalize;
  color: #fff;
}
.pricing .box-container .box .price span {
  font-size: 2rem;
  color: #f2662c;
}
.pricing .box-container .box .btn:hover {
  color: #fff;
}
.pricing .box-container .box .list {
  padding: 1rem 0;
}
.pricing .box-container .box .list p {
  font-size: 1.4rem;
  color: #f2662c;
  line-height: 2;
  color: #f2662c;
}

.projects .heading {
  color: #fff;
}
.projects .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 2rem;
  border-radius: 0.9rem;
}
.projects .box-container .box {
  border-radius: 0.9rem;
  cursor: initial;
}
.projects .box-container .box:hover .image img {
  transform: scale(1.1);
}
.projects .box-container .box .image {
  height: 40rem;
  overflow: hidden;
}
.projects .box-container .box .image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-top-left-radius: 0.9rem;
  border-top-right-radius: 0.9rem;
}
.projects .box-container .box .content {
  display: flex;
  gap: 5rem;
  background: #fff;
  justify-content: space-between;
  border-bottom-left-radius: 0.3rem;
  border-bottom-right-radius: 0.3rem;
}
.projects .box-container .box .content .info {
  padding: 1rem 2rem;
}
.projects .box-container .box .content .info h3 {
  font-size: 1.7rem;
  color: #000;
  text-transform: capitalize;
}
.projects .box-container .box .content .info p {
  font-size: 1.5rem;
  color: #f2662c;
  line-height: 2;
}
.projects .box-container .box .content i {
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.3rem;
  width: 7.7rem;
  font-size: 3rem;
  background: #f2662c;
  color: #fff;
  cursor: pointer;
  text-align: center;
  line-height: 6.5rem;
}

.reviews .slide p {
  padding: 1.5rem;
  background: #141617;
  color: #fff;
  position: relative;
  margin-bottom: 3rem;
}
.reviews .slide p::before {
  content: "";
  position: absolute;
  bottom: -1rem;
  left: 2rem;
  height: 2rem;
  width: 2rem;
  background-color: #141617;
  box-shadow: 0px 1px 10px 0px hsla(0deg, 0%, 0%, 0.12), 0px 2px 4px -1px hsla(0deg, 0%, 0%, 0.2);
  transform: rotate(45deg);
}
.reviews .slide .user {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.reviews .slide img {
  height: auto;
  width: 15%;
}
.reviews .slide h3 {
  font-size: 1.5rem;
  color: #000;
  text-transform: capitalize;
  color: #fff;
  font-weight: 400;
}
.reviews .slide .stars {
  padding-top: 0.5rem;
}
.reviews .slide .stars i {
  font-size: 1.4rem;
  color: #fff;
}

.services .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  gap: 2rem;
}
.services .box-container .box {
  text-align: center;
  padding: 3rem;
  background: #fff;
  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.15);
  border: 0.1rem solid rgba(0, 0, 0, 0.3);
}
.services .box-container .box:hover img {
  transform: translateY(-1rem);
}
.services .box-container .box img {
  height: 7rem;
  margin-bottom: 0.5rem;
  transition: 0.2s linear;
}
.services .box-container .box h3 {
  font-size: 1.7rem;
  color: #000;
  text-transform: capitalize;
  padding: 1rem 0;
}
.services .box-container .box p {
  font-size: 1.4rem;
  color: #f2662c;
  line-height: 2;
  padding: 1rem;
}

.about {
  color: #fff;
}
.about .row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 3rem;
}
.about .row .video {
  flex: 1 1 41rem;
}
.about .row .video video {
  width: 100%;
}
.about .row .content {
  flex: 1 1 41rem;
}
.about .row .content h3 {
  font-size: 3.5rem;
  color: #f2662c;
  line-height: 2;
  color: #fff;
}
.about .row .content p {
  font-size: 1.5rem;
  color: #f2662c;
  line-height: 2;
  padding: 1rem;
  color: #fff;
}
.about .box-container {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(16rem, 1fr));
  gap: 2rem;
  align-items: flex-end;
}
.about .box-container .box {
  text-align: center;
  padding: 3rem 2rem;
}
.about .box-container .box h3 {
  font-size: 4rem;
  color: #f2662c;
  line-height: 2;
}
.about .box-container .box p {
  font-size: 2rem;
  color: #f2662c;
  line-height: 2;
  padding-top: 2rem;
}